import tabbar from '@/components/tabbar.vue'
export default {
	components: {
		tabbar
	},
	data() {
		return {
			tabList: ['提现中', '已到账'],
			tabIndex: 0,
			page: 1,
			subjectList: [
				// {
				// 	time: "2012-5222-5", //提现时间
				// 	price: "255", //提现金额

				// },
				// {
				// 	time: "2012-5222-5",
				// 	price: "244",
				// }
			],
		}
	},
	onLoad() {
		this.getList(1, this.tabIndex)
	},
	onReachBottom() {
		let page = this.page
		page++
		this.page = page
		this.getList(page, this.tabIndex)

	},
	methods: {
		getList(page, index) {
			let type;
			if (index == 0) {
				type = 0
			} else {
				type = 1
			}
			this.$util.get('/login/getWithdrawList?status=' + type + '&page=' + page + '&limit=' + 10).then(res => {
				if (this.subjectList.length == 0) {
					this.subjectList = res.data.data.list
				} else {
					this.subjectList = [...this.subjectList, ...res.data.data.list]
				}
			})
		},
		onChang(index) {
			this.tabIndex = index
				this.subjectList=[]
			this.getList(1,this.tabIndex)
		},
	}
}
